"use client"

import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { ChevronRight, Search, Package, Calendar } from "lucide-react"
import type { Shipment } from "@/components/cargo-loader-app"

interface ShipmentsListPanelProps {
  onToggle: () => void
  shipments: Shipment[]
  selectedShipmentId: string | null
  onSelectShipment: (shipmentId: string) => void
}

export default function ShipmentsListPanel({
  onToggle,
  shipments,
  selectedShipmentId,
  onSelectShipment,
}: ShipmentsListPanelProps) {
  return (
    <div className="flex h-full flex-col">
      {/* Header */}
      <div className="flex items-center justify-between border-b border-gray-200 px-4 py-3">
        <div className="flex items-center gap-2">
          <Package className="h-5 w-5 text-gray-700" />
          <h2 className="text-lg font-semibold text-gray-900">Shipments</h2>
        </div>
        <Button variant="ghost" size="icon" onClick={onToggle}>
          <ChevronRight className="h-5 w-5" />
        </Button>
      </div>

      {/* Search */}
      <div className="border-b border-gray-200 p-4">
        <div className="relative">
          <Search className="absolute left-3 top-1/2 h-4 w-4 -translate-y-1/2 text-gray-400" />
          <Input placeholder="Search Shipments..." className="pl-9" />
        </div>
      </div>

      {/* Shipments List */}
      <div className="flex-1 overflow-y-auto">
        {shipments.length === 0 ? (
          <div className="flex h-full flex-col items-center justify-center p-8 text-center">
            <Package className="mb-4 h-16 w-16 text-gray-300" />
            <h3 className="mb-2 text-lg font-semibold text-gray-900">No Shipments Yet</h3>
            <p className="text-sm text-gray-500">
              Place items in the container and click Save to create your first shipment
            </p>
          </div>
        ) : (
          <div className="divide-y divide-gray-200">
            {shipments.map((shipment) => (
              <button
                key={shipment.id}
                onClick={() => onSelectShipment(shipment.id)}
                className={`w-full px-4 py-4 text-left transition-colors hover:bg-gray-50 ${
                  selectedShipmentId === shipment.id ? "bg-blue-50" : ""
                }`}
              >
                <div className="flex items-start gap-3">
                  {/* Shipment Icon */}
                  <div
                    className={`flex h-12 w-12 items-center justify-center rounded-lg ${
                      selectedShipmentId === shipment.id ? "bg-blue-500" : "bg-teal-500"
                    }`}
                  >
                    <Package className="h-6 w-6 text-white" />
                  </div>

                  {/* Shipment Info */}
                  <div className="flex-1">
                    <h3 className="font-semibold text-gray-900">{shipment.name}</h3>
                    <div className="mt-1 flex items-center gap-2 text-xs text-gray-500">
                      <Calendar className="h-3 w-3" />
                      {new Date(shipment.createdAt).toLocaleDateString()}
                    </div>
                    <div className="mt-2 flex gap-4 text-xs text-gray-600">
                      <span>
                        <span className="font-semibold">{shipment.placedBoxes.length}</span> items
                      </span>
                      <span>
                        <span className="font-semibold">{shipment.container || "No container"}</span>
                      </span>
                    </div>
                  </div>

                  {/* Selection Indicator */}
                  {selectedShipmentId === shipment.id && (
                    <div className="flex h-12 items-center">
                      <div className="h-2 w-2 rounded-full bg-blue-500"></div>
                    </div>
                  )}
                </div>
              </button>
            ))}
          </div>
        )}
      </div>
    </div>
  )
}
